import React from 'react';
import { AppBar, Toolbar, Typography, Container } from '@mui/material';
import { NavLink } from 'next/link';

function NavText({ href, text }) {
    return (
        <Typography
            variant="h6"
            noWrap
            sx={{
                marginRight: '20px',
                fontFamily: 'Roboto, sans-serif',
                fontWeight: 600,
                letterSpacing: '.1rem',
                color: '#ffffff',
                '& a': {
                    color: 'inherit',
                    textDecoration: 'none',
                    transition: 'color 0.3s ease',
                    '&:hover': {
                        color: '#ff0000',
                    },
                },
            }}
        >
            <NavLink href={href}>{text}</NavLink>
        </Typography>
    );
}

export default function NavBar() {
    return (
        <AppBar position="static" sx={{ backgroundColor: '#000000', borderBottom: '2px solid #000000' }}>
            <Container maxWidth="xl">
                <Toolbar disableGutters>
                    <Typography
                        variant="h4"
                        sx={{ flexGrow: 1, fontFamily: 'Pacifico, cursive', color: '#ffffff' }}
                    >
                        PMDB
                    </Typography>
                    <NavText href="/" text="Home" />
                    <NavText href="/search" text="Search" />
                    <NavText href="/recommendation" text="Recommendation" />
                    <NavText href="/profile" text="Profile" />
                    <NavText href="/records" text="Records" />
                </Toolbar>
            </Container>
        </AppBar>
    );
}
