"use client";

import React, { useState, useEffect } from 'react';
import NavBar from '../components/NavBar';
import MovieCard from '../components/MovieCard';

export default function HomePage() {
    const [movies, setMovies] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        // Simulated API call to fetch movies
        setTimeout(() => {
            setMovies([
                { id: 1, title: 'Inception', rating: 8.8, genre: 'Sci-Fi' },
                { id: 2, title: 'The Dark Knight', rating: 9.0, genre: 'Action' },
                { id: 3, title: 'Interstellar', rating: 8.6, genre: 'Adventure' },
            ]);
            setLoading(false);
        }, 1000);
    }, []);

    return (
        <>
            {/* Navigation Bar */}
            <NavBar />

            {/* Page Content */}
            <div className="p-6 bg-gray-900 min-h-screen">
                <h1 className="text-4xl font-bold text-white mb-6">Top Movies</h1>
                {loading ? (
                    <p className="text-white text-lg">Loading...</p>
                ) : (
                    <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
                        {movies.map((movie) => (
                            <MovieCard
                                key={movie.id}
                                title={movie.title}
                                rating={movie.rating}
                                genre={movie.genre}
                                onDetailsClick={() => alert(`Details of ${movie.title}`)}
                            />
                        ))}
                    </div>
                )}
            </div>
        </>
    );
}
