import React from 'react';
import { Card, CardHeader, CardContent, CardActions, Button, Typography } from '@mui/material';

export default function MovieCard({ title, rating, genre, onDetailsClick }) {
    return (
        <Card
            sx={{
                maxWidth: 345,
                margin: 'auto',
                boxShadow: 3,
                borderRadius: 2,
                backgroundColor: '#1a1a1a',
                color: '#ffffff',
            }}
        >
            <CardHeader
                title={title}
                subheader={`Genre: ${genre}`}
                sx={{
                    color: '#ffffff',
                    '& .MuiCardHeader-subheader': {
                        color: '#bbbbbb',
                    },
                }}
            />
            <CardContent>
                <Typography variant="body1">Rating: {rating}</Typography>
            </CardContent>
            <CardActions>
                <Button size="small" onClick={onDetailsClick} sx={{ color: '#ffffff' }}>
                    View Details
                </Button>
            </CardActions>
        </Card>
    );
}
